  font-size: 1rem;
  color: #f4f4f4;
}

section ul {
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  max-width: 100vw;
}

section ul::-webkit-scrollbar {
  display: none;
}

section ul li {
  min-width: 100vw;
  height: 100vh;
  scroll-snap-align: start;
  text-align: center;
  position: relative;
}

